* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  background-color: #eee;
  justify-content: center;

  form {
    width: 95%;
    margin: auto;
    max-width: 560px;

    input,
    textarea {
      width: 100%;
      outline: none;
      margin: 1rem 0;
      padding: 12px;
      font-size: 15px;
      border-radius: 3px;
      border: 1px solid rgb(4, 28, 34);
    }
    textarea {
      height: 300px;
      resize: vertical;
    }
    input[type="submit"] {
      width: 100%;
      color: #fff;
      border: none;
      max-width: 200px;
      cursor: pointer;
      transition: 0.2s;
      background: rgb(34, 92, 44);
      &:hover {
        background: darken(rgb(34, 92, 44), 10%);
      }
    }

    p {
      color: red;
      margin: 0.5rem 0;
      font-size: 0.9rem;
      font-style: italic;
    }
  }
}

.submission-component {
  width: 95%;
  max-width: 560px;
  display: flex;
  padding: 2rem;
  overflow: hidden;
  align-items: center;
  border-radius: 20px;
  justify-content: center;
  box-shadow: 0 0 8px rgba(#000, 0.2);

  div {
    display: flex;
    flex-flow: column;
    align-items: center;

    i {
      font-size: 20rem;
      color: rgb(7, 156, 32);
    }

    h1 {
      color: rgb(7, 156, 32);
      margin: 2rem 0;
    }
  }
}
